<template>
    <div>
        <el-table
                ref="multipleTable"
                :data="mediaList"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectChange">
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    prop="zhTitle"
                    label="标题">
                <template slot-scope="scope">{{scope.row.zhTitle}}</template>
            </el-table-column>
            <el-table-column
                    prop="_id"
                    label="ID">
            </el-table-column>
            <el-table-column
                    prop="difficulty"
                    label="难度">
                <template slot-scope="scope">
                    <span v-if="scope.row.difficulty === 'simple'">初级</span>
                    <span v-if="scope.row.difficulty === 'easily'">中级</span>
                    <span v-if="scope.row.difficulty === 'difficulty'">高级</span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="状态"
                    show-overflow-tooltip>
            </el-table-column>

            <el-table-column
                    label="操作" v-if="tableType === 'media'">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="jumpPage('/editMediaInfo/'+scope.row._id)">编辑</el-button>
                    <el-button type="text" size="small" style="color: #F56C6C;" @click="delMediaInfo(scope.row._id)">刪除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        props:{
            tableType:String,
            mediaList:Array,
        },
        data(){
            return{

            }
        },
        mounted(){

        },
        methods:{
            jumpPage(key) {
                this.$router.push(key)
            },
            delMediaInfo(id){
                this.$emit('delMediaInfo',id)
            },
            handleSelectChange(val){
                this.$emit('select',val)
            },
        }
    }
</script>

<style scoped>

</style>