<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="12">
                        标题
                    </el-col>
                    <div style="float: right">
                        <div>
                            <el-button size="small" style="margin-right: 15px"
                                       @click="jumpPage('/editMediaInfo/'+$route.params.id)">查看源素材
                            </el-button>

                            <el-dropdown split-button type="primary" size="small" @command="handleCommand"
                                         trigger="click">发布
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item :command="{key:'time',value:''}">定时发布</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </div>
                </el-row>

                <el-row class="talkInfo">
                    <el-col :span="3">
                        <div>难度</div>

                        <div>英文字幕</div>

                        <div>标签</div>
                    </el-col>
                    <el-col :span="12">
                        <div>初级</div>
                        <div>I know a repetitive song when I hear one, but translating that intuition into a number
                            isn't
                            easy.
                        </div>
                        <div>魔方秀</div>
                    </el-col>
                </el-row>

                <!--<video width="732" height="403" controls="controls">-->
                <!--<video src="https://vd3.bdstatic.com/mda-ii4s1unvxbkg3kkq/sc/mda-ii4s1unvxbkg3kkq.mp4"></video>-->
                <!--</video>-->
                <embed src='https://player.youku.com/player.php/sid/XMTQ3MjM5Mjc0MA==/v.swf' allowFullScreen='true'
                       quality='high' width='480' height='400' align='middle' allowScriptAccess='always'
                       type='application/x-shockwave-flash'></embed>

                <release-timing :isShow="isShowSetTime" @hideTiming="isShowSetTime = false"/>

            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'

    export default {
        components: {
            ReleaseTiming,
            LeftAside
        },
        data() {
            return {
                isShowSetTime: false,
            }
        },
        mounted(){
            this.getOneTalkData();
        },
        methods: {
            jumpPage(key) {
                this.$router.push(key)
            },
            handleCommand(command) {
                console.log(command)
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                }
            },
            getOneTalkData() {
                this.$axios.get('/lesson/_getone', {params: {lessonId: this.$route.params.id}}).then((res) => {
                    if (!res.data.code) {
                        console.log(res.data.data);
                    }
                })
            },
        },
    }
</script>

<style scoped>
    .talkInfo {
        font-size: 12px;
        padding: 20px 0;
    }

    .talkInfo div {
        margin: 15px 0;
    }
</style>