<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="12">
                        <el-input placeholder="标题" size="mini" v-model="iWordTitle"/>
                    </el-col>
                    <div style="float: right">
                        <div>
                            <el-button size="small" style="margin-right: 15px" @click="postWordsData">保存草稿
                            </el-button>

                            <el-dropdown split-button type="primary" size="small" @click="postReleaseData"
                                         @command="handleCommand"
                                         trigger="click">发布
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item :command="{key:'time',value:''}">定时发布</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </div>
                </el-row>


                <el-row type="flex" class="workInfoMain">
                    <el-col :span="11">
                        <p>Markdown
                            <el-button type="text" style="margin-left: 30px;padding: 0"
                                       @click="dialogTableVisible = true">插入媒体
                            </el-button>
                        </p>
                        <div>
                            <el-input type="textarea" @change="changeMarkDown" v-model="markdownInfo"
                                      :style="{height: currentHeight + 'px'}"/>
                        </div>
                    </el-col>
                    <el-col :span="11" :offset="1">
                        <p>渲染预览</p>
                        <div class="workInfoShow"
                             :style="{height: currentHeight + 'px'}">
                            <vue-markdown :source="showInfo" class="markdownInfo">
                            </vue-markdown>
                        </div>
                    </el-col>
                </el-row>

                <release-timing :isShow="isShowSetTime" :id="$route.params.id" @hideTiming="isShowSetTime = false"/>
            </el-main>


            <el-dialog title="媒体库" width="95%" top="5vh" :visible.sync="dialogTableVisible">
                <filter-list listType="media" :filter-list="filterList" @filterMediaList="filterMediaList"/>

                <media-list table-type="mediaDia" :media-list="mediaList" @select="handleSelectChange"
                            :current-page="currentPage"/>

                <span slot="footer" class="dialog-footer">
                    <el-pagination style="text-align: center"
                                   @current-change="handleCurrentChange"
                                   :current-page.sync="currentPage"
                                   :page-size="10"
                                   layout="total, prev, pager, next"
                                   :total="paginationNum">
                    </el-pagination>
                    <el-button type="primary" size="small" @click="createMDInfo">确 定</el-button>
                </span>
            </el-dialog>
        </el-container>
    </el-container>
</template>

<script>
    import FilterList from '../components/FilterListItem'
    import MediaList from '../components/MediaListItem'
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'

    export default {
        components: {
            ReleaseTiming,
            LeftAside,
            FilterList,
            MediaList,
        },
        data() {
            return {
                iWordTitle: '',
                markdownInfo: '',
                currentHeight: 0,
                showInfo: '',
                isShowSetTime: false,
                dialogTableVisible: false,
                filterList: {},

                mediaList: [],
                currentPage: 1,
                paginationNum: 0,
                currentSelect: [],
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                this.currentHeight = window.innerHeight - 250;
                if(this.$route.params.id !== 'new'){
                    this.getOneWordsData();
                }
                this.getMediaListData();
            },
            getMediaListData(find) {
                find = find ? find : {};
                find.page = this.currentPage;

                this.$axios.get('/material/_find', {params: find}).then((res) => {
                    if (!res.data.code) {
                        this.mediaList = res.data.data
                    }
                });
                this.getMediaNumData(find);
            },
            getMediaNumData(find) {
                find = find ? find : {};
                this.$axios.get('/material/_getnum', {params: find}).then((res) => {
                    this.paginationNum = res.data.data;
                })
            },
            getOneWordsData() {
                this.$axios.get('/lesson/_getone', {params: {lessonId: this.$route.params.id}}).then((res) => {
                    if (!res.data.code) {
                        console.log(res.data.data)
                        this.iWordTitle = res.data.data.title || '';
                        this.markdownInfo = res.data.data.cms;
                    }
                })
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getMediaListData();
            },
            handleCommand(command) {
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                }
            },
            filterMediaList() {

            },
            postWordsData(){
                if(this.$route.params.id==='new'){
                    this.postAddWordsData();
                }else {
                    this.postUpdateWordsData();
                }
            },
            postAddWordsData(){
                let data = new FormData();
                data.append('cms', this.markdownInfo);
                data.append('zhTitle', this.iWordTitle);
                data.append('category', 'word');
                data.append('status', 0);

                this.$axios.post('/lesson/_add', data).then((res) => {
                    console.log(res);
                    if (!res.data.code) {
                        this.$message.success('添加成功');
                    }
                })
            },
            postUpdateWordsData() {
                let data = new FormData();
                data.append('lessonId', this.$route.params.id);
                data.append('cms', this.markdownInfo);
                data.append('zhTitle', this.iWordTitle);

                this.$axios.post('/lesson/_update', data).then((res) => {
                    console.log(res);
                    if (!res.data.code) {
                        this.$message.success('更新成功');
                    }
                })
            },
            postReleaseData() {
                let data = new FormData();
                data.append('lessonId', this.$route.params.id);
                data.append('status', 2);

                this.$axios.post('/lesson/_update', data).then((res) => {
                    console.log(res);
                    if (!res.data.code) {
                        this.$message.success('发布成功');
                    }
                })
            },
            handleSelectChange(val) {
                let all = [];
                val.forEach(value => {
                    all.push(this.getOneFile(value.fileId));
                });

                Promise.all(all).then((result) => {
                    console.log(result)
                    this.currentSelect = result;
                })
            },
            getOneFile(id) {
                return new Promise((resolve, reject) => {
                    this.$axios.get('/file/_getone', {params: {fileId: id}}).then((res) => {
                        if (!res.data.code) {
                            resolve(res.data.data);
                        }
                    })
                });
            },
            createMDInfo() {
                let mp3Info = '![audio]()';
                let str = '';
                this.currentSelect.forEach(value => {
                    let file = value.fileName.substring(value.fileName.length - 4);
                    if (file === '.mp3') {
                        str += '![audio](' + value.fileUrl + ')\n';
                    } else if (file === 'mp4') {
                        str += '![video](' + value.fileUrl + ')\n';
                    }
                });

                this.markdownInfo += str;
                this.dialogTableVisible = false;
            },

            changeMarkDown() {
                let result, videoResult;
                let audioStr = /!+\[audio]/gi;
                let mp3Str = /\.mp3\)/gi;
                let videoStr = /!+\[video]/gi;
                let mp4Str = /\.mp4\)/gi;
                let info = this.markdownInfo;
                while ((videoResult = videoStr.exec(info)) != null) {
                    mp4Str.exec(info);
                    let mp4SubStr = info.substring(videoStr.lastIndex + 1, mp4Str.lastIndex - 1);
                    let videoHtml = `<video id="video${videoStr.lastIndex}" controls="" preload="none" poster="">
      <source id="mp4${videoStr.lastIndex}" src="${mp4SubStr}" type="video/mp4"></video>`;

                    if (videoStr !== -1) {
                        info = info.substr(0, videoStr.lastIndex - 8) + videoHtml + info.substr(mp4Str.lastIndex, info.length - 1);
                    }
                }

                while ((result = audioStr.exec(info)) != null) {
                    mp3Str.exec(info);
                    let mp3SubStr = info.substring(audioStr.lastIndex + 1, mp3Str.lastIndex - 1);
                    let audioHtml = `<audio id="audio${audioStr.lastIndex}" controls="" preload="none">
                    <source id="mp3${audioStr.lastIndex}" src="${mp3SubStr}"></audio>`;

                    if (mp3SubStr !== -1) {
                        info = info.substr(0, audioStr.lastIndex - 8) + audioHtml + info.substr(mp3Str.lastIndex, info.length - 1);
                    }
                }
                this.showInfo = info;
            },
        },
        watch: {
            markdownInfo(newInfo, old) {
                this.changeMarkDown();
            }
        }
    }
</script>

<style scoped>
    .workInfoMain {
        margin-top: 46px;
        font-size: 13px
    }

    .workInfoMain p {
        margin-bottom: 8px;
    }

    .workInfoShow {
        overflow-y: auto;
        border: 1px solid #dcdfe6;
        border-radius: 5px;
        min-height: 20px;
    }

    .markdownInfo {
        padding: 5px 15px;
    }
</style>